import React, { useRef, useEffect } from 'react';
import { cn } from '../lib/utils';
import { Button } from "./ui/button";

export const EntryBlock = ({
    note,
    onSave,
    onInput,
    onFocus,
    autoFocus = false,
    isLast = false
}) => {
    const contentRef = useRef(null);

    // Auto-resize or just standard div block?
    // Since it's contentEditable div, it grows automatically.

    useEffect(() => {
        if (autoFocus && contentRef.current) {
            contentRef.current.focus();
            // Move cursor to end
            const range = document.createRange();
            range.selectNodeContents(contentRef.current);
            range.collapse(false);
            const sel = window.getSelection();
            sel.removeAllRanges();
            sel.addRange(range);
        }
    }, [autoFocus]);

    const handleInput = (e) => {
        if (onInput) {
            onInput(note.id, e.target.innerText);
        }
    };

    const handleBlur = (e) => {
        if (onSave) {
            onSave(note.id, e.target.innerText);
        }
    };

    // Convert tags in content for display? 
    // We need a way to render tags nicely while editing.
    // For now, let's keep it raw text like the previous editor, but maybe style it?

    // Actually, separating display/edit is hard in contentEditable without a library.
    // We will stick to the previous implementation: just text.

    return (
        <div id={note.id} className="group relative w-full max-w-3xl mx-auto py-4">
            {/* Content */}
            <div
                ref={contentRef}
                contentEditable
                suppressContentEditableWarning
                onInput={handleInput}
                onBlur={handleBlur}
                onFocus={() => onFocus && onFocus(note)}
                className="w-full min-h-[1.5em] outline-none text-lg text-foreground whitespace-pre-wrap leading-relaxed empty:before:content-[attr(data-placeholder)] empty:before:text-muted-foreground/40"
                data-placeholder={isLast ? "Write something..." : ""}
            >
                {note.content}
            </div>

            {/* Hover Controls (Delete, etc - Minimalist) */}
            <div className="absolute -right-12 top-4 opacity-0 group-hover:opacity-100 transition-opacity">
                <Button
                    variant="ghost"
                    size="icon"
                    className="h-8 w-8 text-muted-foreground hover:text-destructive"
                    onClick={() => {
                        // Trigger deletion by saving empty content or passing a specific delete prop?
                        // The parent handles delete if content is empty. So we can clear content and blur?
                        // Or better to pass onDelete prop.
                        if (contentRef.current) {
                            contentRef.current.innerText = "";
                            contentRef.current.blur(); // Triggers onSave with empty string -> Delete
                        }
                    }}
                >
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-4 h-4">
                        <path strokeLinecap="round" strokeLinejoin="round" d="m14.74 9-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 0 1-2.244 2.077H8.084a2.25 2.25 0 0 1-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 0 0-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 0 1 3.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 0 0-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 0 0-7.5 0" />
                    </svg>
                </Button>
            </div>
        </div>
    );
};
