import React from 'react';
import { cn } from '../lib/utils';

// Helper to find the first note ID for a given date
const getNoteIdForDate = (notes, year, month, day) => {
    const note = notes.find(n => {
        const d = new Date(n.createdAt);
        return d.getFullYear() === year && d.getMonth() === month && d.getDate() === day;
    });
    return note ? note.id : null;
};

export const TimelineRail = ({ notes, activeNoteId, tags, onTagClick }) => {
    // Group notes by sorted keys to render in order
    // But we don't display the group headers anymore.
    const groupedDates = React.useMemo(() => {
        const groups = {};
        notes.forEach(note => {
            const date = new Date(note.createdAt);
            const year = date.getFullYear();
            const month = date.getMonth(); // 0-11
            const day = date.getDate();

            if (!groups[year]) groups[year] = {};
            if (!groups[year][month]) groups[year][month] = new Set();
            groups[year][month].add(day);
        });
        return groups;
    }, [notes]);

    // Create a map of "Date String -> Note ID" for instant lookup
    // Ideally we want to highlight the DATE if ANY note from that date is active.
    // So we need to know: Does the activeNoteId belong to this date?
    const activeDateKey = React.useMemo(() => {
        if (!activeNoteId) return null;
        const note = notes.find(n => n.id === activeNoteId);
        if (!note) return null;
        const d = new Date(note.createdAt);
        return `${d.getFullYear()}-${d.getMonth()}-${d.getDate()}`;
    }, [activeNoteId, notes]);

    const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

    return (
        <div className="fixed left-8 top-0 bottom-0 w-24 z-40 hidden sm:flex flex-col justify-center pointer-events-none">
            {/* Inner container for scrolling if needed, max height constraint */}
            <div className="max-h-[80vh] overflow-y-auto no-scrollbar py-4 flex flex-col items-center">
                <div className="flex flex-col gap-1 w-full px-2">
                    {Object.entries(groupedDates).sort(([a], [b]) => a - b).map(([year, months]) => (
                        <React.Fragment key={year}>
                            {Object.entries(months).sort(([a], [b]) => a - b).map(([month, days]) => (
                                <React.Fragment key={`${year}-${month}`}>
                                    {Array.from(days).sort((a, b) => a - b).map(day => {
                                        // Precise Key Matching
                                        const currentKey = `${year}-${month}-${day}`;
                                        const isSameDate = activeDateKey === currentKey;

                                        // Click handler
                                        const handleDateClick = () => {
                                            const noteId = getNoteIdForDate(notes, parseInt(year), parseInt(month), day);
                                            if (noteId) {
                                                const element = document.getElementById(noteId);
                                                if (element) {
                                                    element.scrollIntoView({ behavior: 'smooth', block: 'center' });
                                                }
                                            }
                                        };

                                        return (
                                            <div
                                                key={`${year}-${month}-${day}`}
                                                onClick={handleDateClick}
                                                className="group flex items-center justify-start w-full gap-3 h-3 cursor-pointer pointer-events-auto"
                                            >
                                                {/* Ruler Tick (Left side) */}
                                                <div className={cn(
                                                    "h-[1.5px] rounded-full transition-all duration-300 group-hover:w-6 group-hover:bg-foreground group-hover:opacity-100",
                                                    isSameDate
                                                        ? "w-6 bg-foreground opacity-100"
                                                        : "w-2 bg-muted-foreground opacity-30"
                                                )} />

                                                {/* Date Label (Appears on Right of tick) */}
                                                <span className={cn(
                                                    "text-[11px] font-medium tracking-wide transition-all duration-300 whitespace-nowrap group-hover:opacity-100 group-hover:translate-x-0 group-hover:text-foreground",
                                                    isSameDate
                                                        ? "opacity-100 text-foreground translate-x-0"
                                                        : "opacity-0 text-muted-foreground -translate-x-4"
                                                )}>
                                                    {monthNames[parseInt(month)]} {day}
                                                </span>
                                            </div>
                                        );
                                    })}
                                </React.Fragment>
                            ))}
                        </React.Fragment>
                    ))}
                </div>
            </div>
        </div>
    );
};
