import React, { useEffect, useRef } from 'react';
import { EntryBlock } from './EntryBlock';
import { cn } from '../lib/utils';

export const NotebookFeed = ({
    notes,
    onUpdateNote,
    onCreateNote,
    onDeleteNote,
    onFocusBox
}) => {
    const feedRef = useRef(null);
    const bottomRef = useRef(null);

    // Sort notes: Oldest -> Newest for "Notebook" feel
    const sortedNotes = React.useMemo(() => {
        return [...notes].sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt));
    }, [notes]);

    // Group by date for visual separation? 
    // User said: "It will look like one single document in it's entirity. ... There will not be any visual distinction between the notes from multiple days. Except the spacing between them."

    // So we just map them with some margin.

    // Auto-scroll to bottom on load and when notes change (if at bottom?)
    // For now, force scroll to bottom on mount.
    useEffect(() => {
        if (bottomRef.current) {
            // Small timeout to ensure layout is complete
            setTimeout(() => {
                bottomRef.current.scrollIntoView({ behavior: 'auto' });
            }, 100);
        }
    }, [sortedNotes.length]); // Re-run if notes count changes (initial load)

    // Intersection Observer to track scroll position
    useEffect(() => {
        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        const noteId = entry.target.getAttribute('data-note-id');
                        // Use loose equality to match string data-attr with potentially number ID
                        const note = notes.find(n => n.id == noteId);
                        if (note) {
                            onFocusBox(note);
                        }
                    }
                });
            },
            {
                // root: null (viewport) is better if we are scrolling the window or a large container
                // But here we are scrolling the div ref={feedRef}. 
                // Let's rely on viewport for simplicity or ensure feedRef is correct.
                root: feedRef.current,
                threshold: 0, // Trigger as soon as it touches the zone
                rootMargin: "-45% 0px -45% 0px" // 10% center strip (bridges gaps)
            }
        );

        const noteElements = document.querySelectorAll('.entry-block');
        noteElements.forEach(el => observer.observe(el));

        return () => observer.disconnect();
    }, [notes, onFocusBox, sortedNotes]); // Added sortedNotes to dependency to re-attach if list changes

    return (
        <div className="flex-1 h-full overflow-y-auto bg-background scroll-smooth" ref={feedRef}>
            <div className="min-h-full w-full max-w-4xl mx-auto px-8 py-12 sm:pl-32"> {/* Left padding for Rail */}
                <div className="flex flex-col gap-12"> {/* Gap between entries */}
                    {sortedNotes.map((note, index) => (
                        <div key={note.id} className="entry-block" data-note-id={note.id}>
                            <EntryBlock
                                note={note}
                                onSave={(id, content) => {
                                    if (!content || !content.trim()) {
                                        onDeleteNote(note.id);
                                    } else {
                                        onUpdateNote(note, content);
                                    }
                                }}
                                onInput={(id, content) => {
                                    // Optional: live update state?
                                }}
                                // Removing onFocus passing here as observer handles it
                                isLast={index === sortedNotes.length - 1}
                            />
                        </div>
                    ))}
                </div>

                {/* Bottom spacer and generic "New Entry" area if needed */}
                <div
                    ref={bottomRef}
                    className="h-[50vh] w-full cursor-text"
                    onClick={() => {
                        // If last note is empty, focus it. Else create new one.
                        const lastNote = sortedNotes[sortedNotes.length - 1];
                        if (lastNote && !lastNote.content.trim()) {
                            // Focus logic needs to be lifted or handled differently
                        } else {
                            onCreateNote('');
                        }
                    }}
                />
            </div>
        </div>
    );
};
